import './../App.css';
import { useState } from 'react';

function Blog() {

  let [title, setTitle] = useState(['남자코트 추천','여자코트 추천','공용코트 추천'])
  let [updateDate, setUpdateDate] = useState(['2월 17일 발행','4월 27일 발행','12월 24일 발행'])
  let [favorit, setFavorit] = useState([10,2,30])
  let [modal, setModal] = useState(false)
  let [seleted, setSeleted] = useState(0)
  let [newTitle, setNewTitle] = useState('')


  function updatFavorit(num){
    let copy = [...favorit]
    copy[num] = favorit[num]+1
    setFavorit(copy)
  }

  function sortABC(){
    const sorteItem = [...title].sort((a,b) =>  a.localeCompare(b,"ko"))
    setTitle(sorteItem)
  }
  
  return (
    <div className="container">
      <div className="black-nav">
        <h4>블로그임</h4>
      </div>
      <div>
        <button onClick={()=>sortABC()}>정렬</button>
      </div>

      
      {
        title.map(function(a, i){
          return(
          <div className='list'>
            <h4>
            <span onClick={()=> {
              setSeleted(i)
              setModal(true)}}>{ title[i] }</span>
            <span onClick={()=>updatFavorit(i)}> { favorit[i] }</span></h4>
            <p>{ updateDate[i] }</p>
            <button onClick={()=>{
              let copy1 = [...title]
              console.log(copy1)
              copy1.splice(i, 1)
              console.log(copy1)
              setTitle(copy1)
              let copy2 = [...favorit]
              copy2.splice(i,1)
              setFavorit(copy2)
              let copy3 = [...updateDate]
              copy3.splice(i,1)
              setUpdateDate(copy3)
            }}>삭제</button>
          </div>)
        })
      }

      <div className='newArticle'>
        <input id="newTitle" onChange={(e)=> setNewTitle(e.target.value)}></input>
        <button onClick={()=>{
          let copy1 = [...title]
          copy1.unshift(newTitle)
          setTitle(copy1)
          let copy2 = [...favorit]
          copy2.unshift(0)
          setFavorit(copy2)
          let copy3 = [...updateDate]
          copy3.unshift('2월 17일 발행')
          setUpdateDate(copy3)
        }}>글발행</button>
      </div>

      {
        modal == true ? <Modal title={title} seleted={seleted}></Modal> : null
      }
    </div>
  );
}

function Modal(props){
  return(
    <div className='modal'>
      <h4>{props.seleted}</h4>
      <h4>{props.title[props.seleted]}</h4>
      <p>날짜</p>
      <p>상세내용</p>
      <button>닫기</button>
    </div>
  )
}


export default Blog;
